import { Howl } from "howler";
import click from "../asssets/audio/click.mp3";
import succes from "../asssets/audio/succes.mp3";
import fon from "../asssets/audio/fon.mp3";


const audio = {
  click: (volume) => {
    return new Howl({
      src: [click],
      volume: volume,
    });
  },
  succes: (volume) => {
    return new Howl({
      src: [succes],
      volume: volume,
    });
  },
  fon: (volume) => {
    return new Howl({
      src: [fon],
      volume: volume * 0.8,
      loop: true,
    });
  },
};
export default audio;
