import React from "react";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import rss from "../asssets/images/rss.png";
import GitHubIcon from "@material-ui/icons/GitHub";
import Typography from "@material-ui/core/Typography";
import Link from "@material-ui/core/Link";
import { useSelector } from "react-redux";


const useStyles = makeStyles((theme) => ({
  root: {
    marginTop: "auto",
  },
  container: {
    display: "flex",
    flexWrap:"wrap",
    alignItems: "center",
    backgroundColor: "black",
    width: "100%",
    minHeight: "80px",
  },
  logoRss: {
    marginLeft: "5%",
    color: "white",
  },
  git: {
    display: "flex",
    marginLeft: "5%",
    color: "white",
    fontSize: "40px",
    textDecoration: "none",
    gap: "20px",
    "&:hover": {
      color: prop=>prop.color,
      textDecoration: "none",
    }
  },
  rss: {
    width: "100%",
    "&:hover": {
      color: '#f50057',
      textDecoration: "none",
    }
  },
  year: {
    marginLeft: "5%",
    marginRight: "auto",
    color: "white",
    fontSize: "20px",
  },
}));
const Footer = () => {
  const color = useSelector((state) => state.settings.Color);
  const prop = {color}
  const classes = useStyles(prop);
  return (
    <div className={classes.root}>
      <Box className={classes.container}>
        <Link
          href="https://rs.school/"
          className={classes.logoRss}
        >
          <Box width={100}>
            <img className={classes.rss} src={rss} alt="rss logo" />
          </Box>
        </Link>
        <Link
          href="https://github.com/Nicolay-kr"
          className={classes.git}
        >
          <GitHubIcon />
          <Typography ml={40} component="h1" variant="h5">
            nicolay-kr
          </Typography>
        </Link>
        <Typography
          className={classes.year}
          ml={40}
          component="h1"
          variant="h5"
        >
          2021
        </Typography>
      </Box>
    </div>
  );
};

export default Footer;
