import React from "react";
import Flippy, { FrontSide, BackSide } from 'react-flippy';

export default class Flip extends React.Component {
  constructor(props) {
    super(props);
    this.state = {isFlipped:false};
    this.flipp= React.createRef();
  }
  render() {
    return (
  // <div onClick={this.click.bind(this)}>
  <div onClick={this.props.click} className={'cardBody'}>
      <Flippy
        flipOnHover={false} // default false
        flipOnClick={true} // default false
        flipDirection="horizontal" // horizontal or vertical
        isFlipped={this.props.isFlipped}
        style={this.props.isActive? { width: '100px', height: '100px',cursor:'pointer',border:"6px solid red",boxSizing:"border-box"}:{ width: '100px', height: '100px',cursor:'pointer',boxSizing:"border-box"}}
      >
        <FrontSide>
        <img className="backCard" src={this.props.back} alt="back" />
        </FrontSide>
        <BackSide
          style={{ backgroundColor: 'white'}}>
          {this.props.front}
        </BackSide>
      </Flippy>
  </div>
    )
      
  }
}
