import { createSlice } from "@reduxjs/toolkit";
import black from '../asssets/images/blackback.jpg'
const settings = JSON.parse(localStorage.getItem('settings'));

export const settingSlice = createSlice({
  name: "Settings",
  initialState: {
    Board: settings?settings.Board?settings.Board:8:8,
    SoundVolume: settings?settings.SoundVolume?settings.SoundVolume:50:50,
    MusicVolume: settings?settings.MusicVolume?settings.MusicVolume:50:50,
    CardBack: settings?settings.CardBack?settings.CardBack:black:black,
    Color:settings?settings.storeColor?settings.storeColor:"#f50057":"#f50057",
  },

  reducers: {
    changeBoard: (state, action) => {
      state.Board = action.payload;
    },
    changeCardBack: (state, action) => {
      state.CardBack = action.payload;
    },
    changeSoundVolume: (state, action) => {
      state.SoundVolume = action.payload;
    },
    changeMusicVolume: (state, action) => {
      state.MusicVolume = action.payload;
    },
    changeColor: (state, action) => {
      state.Color = action.payload;
    },
  },
});

export const {
  changeMusicVolume,
  changeSoundVolume,
  changeBoard,
  changeCardBack,
  changeColor,

} = settingSlice.actions;

export default settingSlice.reducer;
