import React, { useState, useEffect, useRef } from "react";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import Flip from "./Flip";
import { ALLCARDS } from "../const/cards";
import _ from "lodash";
import audio from "../const/audio";
import Typography from "@material-ui/core/Typography";
import Stopwatch from "./Stopwatch";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import VolumeUpIcon from "@material-ui/icons/VolumeUp";
import VolumeOffIcon from "@material-ui/icons/VolumeOff";
import { Howler } from "howler";
import Paper from "@material-ui/core/Paper";
import { useSelector } from "react-redux";
import { useHttp } from "../hooks/http.hook";
import { routes } from "../utils/routes.js";
import useMediaQuery from '@material-ui/core/useMediaQuery';


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    display: "flex",
    position: "relative",
    flexWrap:"wrap",
  },
  cardBoard: {
    margin: prop=>prop.matches? "20px auto":"auto",
    display: "flex",
    flexWrap: "wrap",
    gap: "25px",
    width: "500px",
  },
  gameStat: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    width: prop=>prop.matches? "auto":"200px",
    margin: prop=>prop.matches? "15px auto":"15px 0 15px auto"
  },
  gameStatRow:{
    display:"flex",
    justifyContent: "center",
    alignItems: "center",
    flexDirection: "row",
    gap:"50px",
    margin:"auto",
    
  },
  card: {
    width: "100px",
    height: "100px",
  },
  points: {
    width: "150px",
    height: "150px",
    backgroundColor: "#ffebeb",
    color: "black",
  },
  button: {
    width: "200px",
    height: "45px",
    fontSize: "20px",
    fontWeight: "500",
    color: "black",
    borderColor: prop=>prop.color,
    marginTop: "20px",
    "&:hover": {
      backgroundColor: "#ffebeb",
    },
  },
  buttonActive: {
    backgroundColor: "#ffebeb",
  },
  volume: {
    cursor: "pointer",
    alignSelf: "flex-end",
  },
  volumeIcon: {
    fontSize: "40px",
  },
  popup: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#ffebeb",
    width: "400px",
    height: "400px",
    position: "absolute",
    top: `50%`,
    left: `50%`,
    transform: prop=>prop.matches? "translate(-50%, -50%)":"translate(-20%, -50%)",
    zIndex: "5",
    opacity: "0.9",
  },
  winText: {
    textAlign: "center",
  },
  winButton: {
    backgroundColor: "black",
    color: "white",
    "&:hover": {
      backgroundColor: prop=>prop.color,
    },
  },
  gameStatInside:{
    margin:"0px auto",
    width:"200px",
  },
  activeCard:{
    border:"6px solid"
  }
}));

const Game = () => {
  const gameStore = JSON.parse(localStorage.getItem('game'));
  const color = useSelector((state) => state.settings.Color);
  const matches = useMediaQuery("(max-width:800px)");
  const Board = useSelector((state) => state.settings.Board);
  const userName = useSelector((state) => state.login.Name);
  const CardBack = useSelector((state) => state.settings.CardBack);
  const MusicVolume = useSelector((state) => state.settings.MusicVolume);
  const SoundVolume = useSelector((state) => state.settings.SoundVolume);
  const field = Board;
  const prop = { color,matches}
  const classes = useStyles(prop);
  const arrFirst = ALLCARDS.slice(0, field / 2);
  const arrSecond = _.cloneDeep(arrFirst);
  let cardList = gameStore?gameStore.cards : [...arrFirst, ...arrSecond].sort(() => Math.random() - 0.5);
  const [cards, setCards] = useState(cardList);
  const [count, setCount] = useState(gameStore? gameStore.count : 0);
  const [openedCard, setOpenedCard] = useState([]);
  const [matched, setMatched] = useState(gameStore?gameStore.matched:[]);
  const [stopWatchActive, setStopWatchActive] = useState(false);
  const [pause, setPause] = useState(false);
  const [play, setPlay] = useState(false);
  const [resetTime, setResetTime] = useState(false);
  const [volume, setVolume] = useState(MusicVolume);
  const [popup, setPopup] = useState(false);
  const {request} = useHttp();
  const  fon = audio.fon(MusicVolume/100)
  const  click = audio.click(SoundVolume/100);
  const  succes = audio.succes(SoundVolume/100); 
  const time = useRef(null);
  const gameTime = time.current?`${
    time.current.state.min < 9
      ? `0${time.current.state.min}`
      : `${time.current.state.min}`
  }:${
    time.current.state.sec < 9
      ? `0${time.current.state.sec}`
      : `${time.current.state.sec}`
  }`:"00:00";

  const clickOnCard = (id, index, item) => {
    
    if (!pause && play) {
      if (!item.isFlipped) {
        click.play();
        setCount((count) => count + 1);
        setOpenedCard((opened) => [...opened, index]);
      }
    }
  };
  useEffect(() => { 
    localStorage.setItem(
      "game",
      JSON.stringify({
        cards: cards,
        count: count,
        matched:matched,
      })
    );
    
  }, [cards, count, matched])

  

  useEffect(() => {
    if (openedCard.length < 2) return;

    const firstMatched = cards[openedCard[0]];
    const secondMatched = cards[openedCard[1]];

    if (!openedCard[2] && secondMatched && firstMatched.id === secondMatched.id) {
      succes.play();
      setMatched((state)=>[...matched, firstMatched.id]);
    }
    if (openedCard.length === 2) setTimeout(() => setOpenedCard([]), 500);
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [openedCard]);

  async function sendStat(stat) {
    try {
        await request(routes.statistic, "POST", {...stat});
    }
    catch (e) {console.log(e)}
  }

  // win
  useEffect(() => {
    if (matched.length > 0 && matched.length * 2 === cards.length) {
      setStopWatchActive(false);
      Howler.stop();
      setPopup(true);
      const stat = {"name":userName,"board":`${Board}`,"time":`${gameTime}`, "moves":`${count}`}

      sendStat(stat)
    }
  // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [cards.length, matched]);

  useEffect(() => { 
    return () => {
      Howler.stop();
      setStopWatchActive(false);
    }
  }, [])



  const handlePause = () => {
    setStopWatchActive((state) => !state);
    setPause((state) => !state);
  };
  const handlePlay = () => {
    if (!pause) {
      Howler.stop();
      fon.play();
      setResetTime(false);
      setStopWatchActive(true);
      setPlay(true);
    }
  };
  const handleNewGame = () => {
    Howler.stop();
    cardList = [...arrFirst, ...arrSecond].sort(() => Math.random() - 0.5);
    setCards(cardList);
    setCount(0);
    setOpenedCard([]);
    setMatched([]);
    setPlay(false);
    setResetTime(true);
    localStorage.removeItem("game");
  };
  const handleVolume = () => {
    if (volume) {
      setVolume(0);
      Howler.volume(0);
    } else {
      setVolume(1);
      Howler.volume(1);
    }
  };

  const handlePopupButton = () => {
    setPopup(false);
    handleNewGame();
    setMatched([]);
  };
  return (
    <div className={classes.root}>
      {popup ? (
        <Paper className={classes.popup} elevation={3}>
          <Typography className={classes.winText} variant="h1">
            Congrats You Win!{" "}
          </Typography>
          <Button
            onClick={handlePopupButton}
            className={`${classes.button} ${classes.winButton}`}
          >
            OK
          </Button>
        </Paper>
      ) : null}
      <Box className={matches?`${classes.gameStatRow}`: classes.gameStat}>
        <Box className={`${classes.gameStat} ${classes.gameStatInside}`}>
          <Box className={classes.volume} onClick={handleVolume}>
            {volume ? (
              <VolumeUpIcon className={classes.volumeIcon} fontSize="large" />
            ) : (
              <VolumeOffIcon className={classes.volumeIcon} fontSize="large" />
            )}
          </Box>
          <Avatar className={classes.points}>
            <Typography variant="h1">{count}</Typography>
          </Avatar>
          <Stopwatch
            ref={time}
            isActive={stopWatchActive}
            resetTime={resetTime}
          />
        </Box>
        <Box className={`${classes.gameStatInside}`}>
            <Button
              onClick={handlePlay}
              variant="outlined"
              className={classes.button}
            >
              Start Game
            </Button>
            <Button
              onClick={handleNewGame}
              variant="outlined"
              className={classes.button}
            >
              New Game
            </Button>
            <Button
              onClick={handlePause}
              variant="outlined"
              className={
                pause ? `${classes.button} ${classes.buttonActive}` : classes.button
              }
            >
              Pause
            </Button>
        </Box>
        
        
      </Box>
      <Box className={classes.cardBoard}>
        {cards.map((item, index) => {
          item.isFlipped =
            openedCard.includes(index) || matched.includes(item.id)
              ? true
              : false;
          return (
            <Flip
              isActive={item.isActive? true:false}
              isFlipped={item.isFlipped}
              click={() => {
                clickOnCard(item.id, index, item);
              }}
              key={index}
              front={
                <img className="backCard" src={item.imageSrc} alt="back" />
              }
              back={CardBack}
            />
          );
        })}
      </Box>
    </div>
  );
};

export default Game;
