import React from "react";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import { NavLink } from "react-router-dom";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import { useSelector } from "react-redux";
import Button from "@material-ui/core/Button";
import { useDispatch } from "react-redux";
import { changeLoginName } from "../store/loginSlice";
import { useAuth } from "../hooks/auth.hook";

const useStyles = makeStyles((theme) => ({
  root: {
    "& > *": {
      marginTop: theme.spacing(0),
    },
  },
  container: {
    display: "flex",
    alignItems: "center",
    backgroundColor: "black",
    width: "100%",
    minHeight: "80px",
    flexWrap: "wrap",
  },
  link: {
    textDecoration: "none",
  },
  linkLogo: {
    marginLeft: "auto",
    marginRight: "auto",
    color: "white",
    fontSize: "40px",
  },
  linkToLogin: {
    display: "flex",
    justifyContent: "center",
    
    margin: (prop) => (prop.matches ? "auto" : "0"),
    padding: "10px",
    fontSize: "25px",
    color: (prop)=>prop.color,
    fontWeight: "500",
  },
  button: {
    height:"30px",
    marginLeft: "20px",
    marginRight: "20px",
    color:prop=>prop.color,
    borderColor:prop=>prop.color,
    "&:hover":{
      borderColor:prop=>prop.color,
    },
  },
  loginBox:{
    width: (prop) => (prop.matches ? "auto" : "300px"),
    display:"flex",
    alignItems:"center",
    justifyContent:"center",
  }
}));
const Header = () => {
  const auth = useAuth()
  const dispatch = useDispatch();
  const userName = useSelector((state) => state.login.Name);
  const color = useSelector((state) => state.settings.Color);
  const matches = useMediaQuery("(max-width:800px)");
  const prop = {color,matches}
  const classes = useStyles(prop);

  const handleLogout = () => {
    dispatch(changeLoginName("Ananim"));
    auth.logout()
  };

  return (
    <div className={classes.root}>
      <Box className={classes.container}>
        <NavLink className={`${classes.link} ${classes.linkLogo}`} to={"/"}>
          MEMORY
        </NavLink>
        <Box className={classes.loginBox}>
          <NavLink
            className={`${classes.link} ${classes.linkToLogin}`}
            to={"/login"}
          >
            {userName === "Ananim" ? "SIGN IN" : userName}
          </NavLink>
          {userName === "Ananim" ? null : (
            <Button
              onClick={handleLogout}
              variant="outlined"
              className={classes.button}
              color="secondary"
            >
              Logout
            </Button>
          )}
        </Box>
      </Box>
    </div>
  );
};

export default Header;
