import React from "react";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Box from "@material-ui/core/Box";
import { NavLink } from "react-router-dom";
import Button from "@material-ui/core/Button";

const useStyles = makeStyles({
  root: {
    display: "flex",
    // justifyContent: "center",
    width: "100%",
    maxWidth: 500,
    marginLeft: "auto",
    padding:"40px",
  },
  conteiner: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "center",
    gap:"20px",
  },
  link:{
    textDecoration:"none"
  },
  button:{
    width:"150px",
    height:"30px",
  }
});

export default function MainPage() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Box className={classes.conteiner}>
        <Typography variant="h2" gutterBottom>
          Description
        </Typography>
        <Typography variant="h5" gutterBottom>
          The memory game is a common children's game played with a set of
          cards. The cards have a pictures on one side and each picture appears
          on two (or sometimes four) cards. The game starts with all the cards
          face down and players take turns to turn over two cards. If the two
          cards have the same picture, then they keep the cards, otherwise they
          turn the cards face down again. The winner is the person with the most
          cards when all the cards have been taken.
        </Typography>
        <NavLink className={classes.link} to={"/game"}>
          <Button className={classes.button} variant="contained" color="secondary">
            Lets Try
          </Button>
        </NavLink>
      </Box>
    </div>
  );
}
