import React, { useEffect, useState } from "react";
import { makeStyles } from "@material-ui/core/styles";
import InputLabel from "@material-ui/core/InputLabel";
import FormControl from "@material-ui/core/FormControl";
import Select from "@material-ui/core/Select";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import MusicVolume from "../components/MusicVolume";
import SoundVolume from "../components/SoundVolume";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";
import cardsBack from "../const/cardsBack";
import { changeBoard } from "../store/settingSlice";

import { changeCardBack, changeColor } from "../store/settingSlice";

const useStyles = makeStyles((theme) => ({
  formControl: {
    margin: theme.spacing(1),
    minWidth: 120,
  },
  selectEmpty: {
    marginTop: theme.spacing(2),
  },
  conteiner: {
    display: "flex",
    flexDirection: "column",
    // justifyContent: "space-around",
    alignItems: "end",
    marginLeft: "auto",
    height: "100%",
    // gap:"40px",
  },
  title: {
    margin: "20px",
  },
  settingBox: {
    display: "flex",
    flexWrap: "wrap",
    justifyContent: "center",
    alignItems: "center",
    gap: "20px",
    margin: "0 auto 15px",
  },
  image: {
    display: "block",
    width: "100px",
    height: "100px",
    cursor: "pointer",
    boxSizing: "border-box",
  },
  volumeBox: {
    flexDirection: "column",
  },
  imageActive: {
    border: "5px solid ",
    borderColor: (prop) => prop.color,
  },
  colorBox:{
    marginBottom:"0px"
  }
}));

const SettingPage = () => {
  const storeColor = useSelector((state) => state.settings.Color);
  const [color, setColor] = useState(storeColor);
  const CardBack = useSelector((state) => state.settings.CardBack);
  const Board = useSelector((state) => state.settings.Board);

  const prop = { color };
  const classes = useStyles(prop);
  const [state, setState] = useState(Board);
  const [cards, setCards] = useState(cardsBack);
  const dispatch = useDispatch();

  const handleChange = (event) => {
    setState(event.target.value);
    dispatch(changeBoard(event.target.value));
    localStorage.removeItem("game");
    localStorage.removeItem("gameTime");
  };
  const changeActiveCardBoard = (name, src) => {
    let newCardsBack = [];
    cards.forEach((item) => {
      newCardsBack.push({
        name: item.name,
        src: item.src,
        isActive: item.name === name ? true : false,
      });
    });
    setCards(newCardsBack);
    dispatch(changeCardBack(src));
  };
  const handleColorChange = (event) => {
    setColor(event.target.value);
    dispatch(changeColor(event.target.value));
  };
  useEffect(()=>{
    localStorage.setItem(
      "settings",
      JSON.stringify({
        storeColor: storeColor,
        CardBack: CardBack,
        Board:Board,
      })
    )
  },[Board, CardBack, storeColor])

  return (
    <div className={classes.conteiner}>
      <Typography className={classes.title} variant="h2">
        Settings
      </Typography>
      <Box className={classes.settingBox}>
        <Typography className={classes.title} variant="h4">
          Choose the board:
        </Typography>

        <FormControl variant="outlined" className={classes.formControl}>
          <InputLabel htmlFor="outlined-age-native-simple">Board</InputLabel>
          <Select
            native
            value={state}
            onChange={handleChange}
            label="Age"
            inputProps={{
              name: "age",
              id: "outlined-age-native-simple",
            }}
          >
            <option value={8}>8 Cards</option>
            <option value={12}>12 Cards</option>
            <option value={16}>16 Cards</option>
          </Select>
        </FormControl>
        <Box className={`${classes.settingBox} ${classes.colorBox}`}>
        <Typography className={classes.title} variant="h4">
          Theme color:
        </Typography>
        <input
          id="color"
          type="color"
          onChange={handleColorChange}
          value={color}
        />
      </Box>
      </Box>
      
      <Box className={classes.settingBox}>
        <Typography className={classes.title} variant="h4">
          Volume:
        </Typography>
        <Box className={`${classes.settingBox} ${classes.volumeBox}`}>
          <Box className={classes.settingBox}>
            <Typography className={classes.title} variant="h5">
              Sounds
            </Typography>
            <SoundVolume></SoundVolume>
          </Box>
          <Box className={classes.settingBox}>
            <Typography className={classes.title} variant="h5">
              Music
            </Typography>
            <MusicVolume></MusicVolume>
          </Box>
        </Box>
      </Box>

      <Box className={classes.settingBox}>
        <Typography className={classes.title} variant="h4">
          Card Back:
        </Typography>
        <Box className={classes.settingBox}>
          {cards.map((item, index) => (
            <img
              value={item.name}
              className={
                item.src === CardBack
                  ? `${classes.image} ${classes.imageActive}`
                  : classes.image
              }
              key={index}
              src={item.src}
              alt="back"
              onClick={() => changeActiveCardBoard(item.name, item.src)}
            />
          ))}
        </Box>
      </Box>
    </div>
  );
};

export default SettingPage;
