import React from "react";

class Stopwatch extends React.PureComponent {
  constructor(props) {

    super(props);
    const gameTime = JSON.parse(localStorage.getItem('gameTime'));
    const min = gameTime? gameTime.min:0;
    const sec = gameTime? gameTime.sec:0;
    this.state = {sec:+sec,min:+min};
  }
  componentDidMount() {
    if(this.props.resetTime){
      this.setState({sec:0,min:0});

    }else{
      if(this.props.isActive){
        setTimeout(() => {
          this.setState((state) => ({
            sec:state.sec + 1
           }));
        },1000)
      }

    }
  }
  componentDidUpdate(){
    if(this.props.resetTime){
      this.setState({sec:0,min:0});

    }else{
      if(this.props.isActive){
        setTimeout(() => {
          if (this.state.sec === 59) {
            this.setState((state) => ({
              sec: 0, min:state.min + 1
            }));
          }
          else{
            this.setState((state) => ({
             sec:state.sec + 1
            }));
          }
        }, 1000);
      }
    }
    localStorage.setItem(
      "gameTime",
      JSON.stringify({
        min: this.state.min,
        sec: this.state.sec,
      })
    )
    
  }
  render() {
    return (
      <div>
      <p className="stopwatch">
        {`${this.state.min < 10 ? `0${this.state.min}` : `${this.state.min}`} : ${
          this.state.sec < 10 ? `0${this.state.sec}` : `${this.state.sec}`
        }`}
      </p>
    </div>
    )
  }
}

  export default Stopwatch;