import React,{useState} from "react";
import Box from "@material-ui/core/Box";
import { makeStyles } from "@material-ui/core/styles";
import { NavLink } from "react-router-dom";

import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import ListItemAvatar from "@material-ui/core/ListItemAvatar";
import SportsEsportsIcon from "@material-ui/icons/SportsEsports";
import Avatar from "@material-ui/core/Avatar";
import EqualizerIcon from "@material-ui/icons/Equalizer";
import SettingsIcon from "@material-ui/icons/Settings";
// import AndroidIcon from "@material-ui/icons/Android";
import HelpOutlineOutlinedIcon from "@material-ui/icons/HelpOutlineOutlined";
import useMediaQuery from "@material-ui/core/useMediaQuery";
import MenuIcon from '@material-ui/icons/Menu';
import { useSelector } from "react-redux";

const useStyles = makeStyles((theme) => ({
  root: {
    marginLeft: "auto",
    height: media=> media.matches800? "auto":"100%",
  },
  container: {
    display: "flex",
    flexWrap: "wrap",
    marginLeft: "auto",
    alignItems: "center",
    backgroundColor: "#ffebeb",
    width: "300px",
    height: "100%",
    justifyContent: "center",
  },
  containerAbs: {
    position:"absolute",
    top:"0",
    right:"0",
    transform: media => media.burger?"translateX(0)": "translateX(300px)",
    transition:"0.5s",
   
  },
  list: {
    width: "100%",
    display: "flex",
    justifyContent: "center",
    flexDirection: "column",
    alignItems: "center",
  },
  listItem: {
    width: "70%",
    fontSize: "40px",
  },
  text: {
    fontSize: "40px",
  },
  link: {
    display: "flex",
    alignItems: "center",
    textDecoration: "none",
    fontSize: "30px",
    color: "black",
    fontWeight: "500",
    "&:hover": {
      color: media=>media.color,
    },
  },
  avatar: {
    backgroundColor: "black",
  },
  burger:{
    position:"absolute",
    top: "-30px",
    right: "20px",
    fontSize:"40px",
    margin:"40px 20px",
    cursor:"pointer",
    zIndex:"5",
    transform: media => media.burger?"rotate(90deg)": "rotate(0deg)",
    transition:"0.5s",
  }
}));
const Menu = () => {
  const color = useSelector((state) => state.settings.Color);
  const matches = useMediaQuery("(max-width:1020px)");
  const matches800 = useMediaQuery("(max-width:800px)");
  const [burger,setBurger]=useState(false)
  const media = {matches800 ,matches,burger,color}
  const classes = useStyles(media);
  const handleBurgerClick =()=>{
    setBurger(state=>!state)

  }
  return (
    <div className={classes.root}>
        {matches ? <MenuIcon onClick={handleBurgerClick} className={classes.burger}/>:null}
        <Box className={matches ? `${classes.containerAbs} ${classes.container}`: classes.container}>
          <List className={classes.list}>
            <ListItem className={classes.listItem}>
              <NavLink className={classes.link} to={"/game"}>
                <ListItemAvatar>
                  <Avatar className={classes.avatar}>
                    <SportsEsportsIcon />
                  </Avatar>
                </ListItemAvatar>
                Game
              </NavLink>
            </ListItem>

            <ListItem className={classes.listItem}>
              <NavLink className={classes.link} to={"/"}>
                <ListItemAvatar>
                  <Avatar className={classes.avatar}>
                    <HelpOutlineOutlinedIcon />
                  </Avatar>
                </ListItemAvatar>
                About
              </NavLink>
            </ListItem>

            <ListItem className={classes.listItem}>
              <NavLink className={classes.link} to={"/statistic"}>
                <ListItemAvatar>
                  <Avatar className={classes.avatar}>
                    <EqualizerIcon />
                  </Avatar>
                </ListItemAvatar>
                Statistic
              </NavLink>
            </ListItem>

            <ListItem className={classes.listItem}>
              <NavLink className={classes.link} to={"/settings"}>
                <ListItemAvatar>
                  <Avatar className={classes.avatar}>
                    <SettingsIcon />
                  </Avatar>
                </ListItemAvatar>
                Settings
              </NavLink>
            </ListItem>
          </List>
        </Box>
    </div>
  );
};

export default Menu;
