import React from "react";
import { makeStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Typography from "@material-ui/core/Typography";
import Slider from "@material-ui/core/Slider";
import Input from "@material-ui/core/Input";
import VolumeUp from "@material-ui/icons/VolumeUp";
import { useDispatch } from "react-redux";
import { useSelector } from "react-redux";

import { changeSoundVolume } from "../store/settingSlice";

const useStyles = makeStyles({
  root: {
    width: 250,
  },
  input: {
    width: 42,
  },
  slider:{
    color:(prop)=>prop.color,
  }
});

export default function SoundVolume() {
  const settings = JSON.parse(localStorage.getItem('settings'));
  const SoundVolume = useSelector((state) => state.settings.SoundVolume);
  const color = useSelector((state) => state.settings.Color);
  const prop = {color}
  const classes = useStyles(prop);
  const [value, setValue] = React.useState(SoundVolume);
  const dispatch = useDispatch();

  const handleSliderChange = (event, newValue) => {
    setValue(newValue);
    dispatch(changeSoundVolume(value));
    localStorage.setItem(
      "settings",
      JSON.stringify({...settings,SoundVolume:newValue
      })
    );
  };

  const handleInputChange = (event) => {
    setValue(event.target.value === "" ? "" : Number(event.target.value));
    console.log("event.target.value");
    dispatch(changeSoundVolume(event.target.value));
  };

  const handleBlur = () => {
    if (value < 0) {
      setValue(0);
    } else if (value > 100) {
      setValue(100);
    }
  };

  return (
    <div className={classes.root}>
      <Typography id="input-slider" gutterBottom>
        Volume
      </Typography>
      <Grid container spacing={2} alignItems="center">
        <Grid item>
          <VolumeUp />
        </Grid>
        <Grid item xs color="primary">
          <Slider
          className={classes.slider}
            value={typeof value === "number" ? value : 0}
            onChange={handleSliderChange}
            aria-labelledby="input-slider"
          />
        </Grid>
        <Grid item>
          <Input
            className={classes.input}
            value={value}
            margin="dense"
            onChange={handleInputChange}
            onBlur={handleBlur}
            inputProps={{
              step: 10,
              min: 0,
              max: 100,
              type: "number",
              "aria-labelledby": "input-slider",
            }}
          />
        </Grid>
      </Grid>
    </div>
  );
}
