import React, { useState, useEffect, useCallback } from "react";
import { withStyles, makeStyles } from "@material-ui/core/styles";
import Table from "@material-ui/core/Table";
import TableBody from "@material-ui/core/TableBody";
import TableCell from "@material-ui/core/TableCell";
import TableContainer from "@material-ui/core/TableContainer";
import TableHead from "@material-ui/core/TableHead";
import TableRow from "@material-ui/core/TableRow";
import Paper from "@material-ui/core/Paper";
import Typography from "@material-ui/core/Typography";
import { useHttp } from "../hooks/http.hook";
import { routes } from "../utils/routes.js";
import CircularProgress from "@material-ui/core/CircularProgress";

const StyledTableCell = withStyles((theme) => ({
  head: {
    backgroundColor: theme.palette.common.black,
    color: theme.palette.common.white,
  },
  body: {
    fontSize: 14,
  },
}))(TableCell);

const StyledTableRow = withStyles((theme) => ({
  root: {
    "&:nth-of-type(odd)": {
      backgroundColor: theme.palette.action.hover,
    },
  },
}))(TableRow);

const useStyles = makeStyles({
  table: {
    width: "100%",
    minWidth: 200,
  },
  conteiner: {
    display: "flex",
    flexDirection: "column",
    justifyContent: "start",
    alignItems: "center",
    flexGrow: 1,
    margin: "20px",
    marginTop: "0",
    maxHeight: "480px",
  },
  title: {
    alignSelf: "end",
    margin: "25px",
  },
});

const StatisticPage = () => {
  const [data, setData] = useState([]);
  const { request } = useHttp();
  const classes = useStyles();

  const fetchStatistic = useCallback(async () => {
    const statistic = await request(routes.statisticall);
    setData(statistic);
  }, [request]);

  useEffect(() => {
    fetchStatistic();
  }, [fetchStatistic]);

  return (
    <div className={classes.conteiner}>
      <Typography className={classes.title} variant="h2">
        Statistic
      </Typography>
      {data.length ? (
        <TableContainer component={Paper}>
          <Table className={classes.table} aria-label="customized table">
            <TableHead>
              <TableRow>
                <StyledTableCell align="center">Name</StyledTableCell>
                <StyledTableCell align="center">Board</StyledTableCell>
                <StyledTableCell align="center">Moves</StyledTableCell>
                <StyledTableCell align="center">Time</StyledTableCell>
              </TableRow>
            </TableHead>
            <TableBody>
              {data.map((item, index) => (
                <StyledTableRow key={index}>
                  <StyledTableCell component="th" scope="row">
                    {item.name}
                  </StyledTableCell>
                  <StyledTableCell align="center">{item.board}</StyledTableCell>
                  <StyledTableCell align="center">{item.moves}</StyledTableCell>
                  <StyledTableCell align="center">{item.time}</StyledTableCell>
                </StyledTableRow>
              ))}
            </TableBody>
          </Table>
        </TableContainer>
      ) : (
        <CircularProgress color="secondary" />
      )}
    </div>
  );
};

export default StatisticPage;
