import React, { useState} from "react";
import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
// import CssBaseline from "@material-ui/core/CssBaseline";
import TextField from "@material-ui/core/TextField";
import FormControlLabel from "@material-ui/core/FormControlLabel";
import Checkbox from "@material-ui/core/Checkbox";
import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
import Box from "@material-ui/core/Box";
import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
import Container from "@material-ui/core/Container";
import { useHttp } from "../hooks/http.hook";
import { useAuth } from "../hooks/auth.hook";
import { routes } from "../utils/routes.js";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";


import {
  changeLoginName,
} from '../store/loginSlice';

function Copyright() {
  return (
    <Typography variant="body2" color="textSecondary" align="center">
      {"Copyright  "}
      <Link color="inherit" href="https://material-ui.com/">
        RS React Game
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}

const useStyles = makeStyles((theme) => ({
  paper: {
    marginTop: theme.spacing(4),
    display: "flex",
    flexDirection: "column",
    alignItems: "center",
    // marginLeft:"0",
  },
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
    marginTop: theme.spacing(1),
  },
  conteiner:{
    marginRight:"0",
  },
  submit: {
    margin: theme.spacing(3, 0, 2),
    backgroundColor:"black",
    color:"white",
    "&:hover": {
      backgroundColor: '#f50057',
    }
  },
}));

export default function SignIn() {
  const auth = useAuth()
  const history = useHistory();
  const dispatch = useDispatch();
  const classes = useStyles();
  // const { loading, error, request, clearError } = useHttp();
  const {request} = useHttp();
  const [isAccount, setIsAccount] = useState(true);
  const [haveAccount, setHaveAccount] = useState(
    "Don't have an account? Sign Up"
  );
  const [form, setForm] = useState({
    name: "",
    email: "",
    password: "",
  });
  const [sign, setSign] = useState("Sign in");

  const handleSignClick = () => {
  
    if (isAccount) {
      setHaveAccount("Have an account? Sign Ip");
      setIsAccount(false);
      setSign("Sign up");
    } else {
      setHaveAccount("Don't have an account? Sign Up");
      setIsAccount(true);
      setSign("Sign in");
    }
  };
  const handleFrormChange = (e) => {
    setForm({ ...form, [e.target.name]: e.target.value });
  };
  async function handleSubmit(e) {
    e.preventDefault();
    try {
      if (!isAccount) {
        console.log(form);
        const data = await request(routes.register, "POST", { ...form });
        
        console.log(data);
        console.log(e);

        setIsAccount(true);
        setForm({
          name: "",
          email: "",
          password: "",
        });
        setSign("Sign in");
        setHaveAccount("Don't have an account? Sign Up");
      } else {
        const data = await request(routes.login, "POST", { ...form });
        dispatch(changeLoginName(data.name));
        let path = `/game`; 
        if(data.name) {
          auth.login(data.token, data.userId, data.name)
          history.push(path);
        }
      }
    } catch (e) {}
  }

  let nameField;
  if (!isAccount) {
    nameField = (
      <TextField
        variant="outlined"
        margin="normal"
        required
        fullWidth
        name="name"
        label="Name"
        type="text"
        id="name"
        autoComplete="name"
        value={form.name}
        onChange={handleFrormChange}
      />
    );
  } else {
    nameField = null;
  }

  return (
    <Container className={classes.conteiner} component="main" maxWidth="xs">
      {/* <CssBaseline /> */}
      <div className={classes.paper}>
        <Avatar className={classes.avatar}>
          <LockOutlinedIcon />
        </Avatar>
        <Typography component="h1" variant="h5">
          {sign}
        </Typography>
        <form className={classes.form} noValidate onSubmit={handleSubmit}>
          {nameField}
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            id="email"
            label="Email Address"
            name="email"
            autoComplete="email"
            autoFocus
            value={form.email}
            onChange={handleFrormChange}
          />
          <TextField
            variant="outlined"
            margin="normal"
            required
            fullWidth
            name="password"
            label="Password"
            type="password"
            id="password"
            autoComplete="current-password"
            value={form.password}
            onChange={handleFrormChange}
          />

          <FormControlLabel
            control={<Checkbox value="remember" color="primary" />}
            label="Remember me"
          />
          <Button
            type="submit"
            fullWidth
            variant="contained"
            color="primary"
            className={classes.submit}
          >
            {sign}
          </Button>
          <Grid container>
            <Grid item>
              <Link href="#" variant="body2" onClick={handleSignClick}>
                {haveAccount}
              </Link>
            </Grid>
          </Grid>
        </form>
      </div>
      <Box mt={4} mb={4}>
        <Copyright />
      </Box>
    </Container>
  );
}